<template>
    <div class="container">
       <div class="main__cars" v-if="todos">
        <div class="main__cars-item" v-for="todo in todos" :key="todo.id">
            <p>ID: {{ todo.id }}</p>
            <p>Title: {{ todo.title }}</p>
            <p>completed: {{ todo.completed }}</p>
        </div>
       </div>
       <div v-else>
        Идет загрузка...
       </div>
    </div>
</template>

<script>
    export default {
        created() {
            this.getTodos()
        },
        computed:{
            todos(){
                return this.$store.state.todos
            }
        },
        methods: {
            getTodos(){
                this.$store.dispatch('getTodos')
            }
        }
    }
</script>